import { Controller } from '@ali/4ever-cangjie';
import onInsertFile from '../handlers/onInsertFile';

export default (uploadFile, autoPreview, configName: string) => (controller: Controller, files) => {
  if (typeof uploadFile !== 'function') {
    throw new Error(`'uploadFile' is missing from ${configName}'s configuration.`);
  }
  return onInsertFile(controller, uploadFile, files, autoPreview);
};
